    data() {
      return {
        // phoneType: 'i5',//i6,i7,i8,i6s,i7s,i8s,ipad,ipad-proiphone5
        list: [{
          title: 'reader', //title
          icon: icon,//
          url: 'https://jiangmengxia.github.io/uniapp-hjfhkshdflsjlfkjn779u/reader',//
        }]
      }
    }
  }
</script>

